<template>
  <div>
    <el-page-header
      style="line-height: 40px; color: silver"
      @back="goBack"
      content=""
    >
    </el-page-header>
    <br />
    <br />
    <el-table :data="tableData" style="width: 100%">
      <el-table-column label="" prop="name"> </el-table-column>
      <el-table-column label="" prop="desc"> </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope" prop="bound_case_info">
          <b style="color: dodgerblue">{{
            scope.row.bound_case_info.case_project_name
          }}</b>
        </template>
      </el-table-column>
      <el-table-column label="" prop="user_info.first_name"> </el-table-column>
      <el-table-column label="" prop="create_date"> </el-table-column>
      <el-table-column align="right">
        <template slot="header" slot-scope="scope">
          {{ (scope = "") }}
          <el-input v-model="search" size="mini" placeholder="" />
        </template>
        <template slot-scope="scope">
          <el-button type="success" @click="switchProject(scope.row)"
            ></el-button
          >
          <el-button
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
            icon="el-icon-edit"
            circle
          ></el-button>
          <el-button
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            icon="el-icon-delete"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row style="margin-top: 30px" type="flex" justify="end">
      <el-pagination
        background
        layout="total, sizes, prev, pager, next"
        :total="totalItems"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="defaultPageSize"
      >
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
import { getProject, deleteProject } from "network/project";
export default {
  data() {
    return {
      tableData: Array(),
      search: "",
      defaultPageSize: 10,
      currentPage: 1,
      totalItems: 0,
    };
  },
  mounted() {
    console.log(this.$store.state);
    getProject(this.defaultPageSize, 0).then((res) => {
      this.tableData = res.results;
      this.totalItems = res.count;
    });
  },
  watch: {
    search: {
      handler() {
        getProject(this.defaultPageSize, 0, this.search).then((res) => {
          this.tableData = res.results;
          this.totalItems = res.count;
        });
      },
    },
  },
  methods: {
    goBack() {
      this.$router.push("/toolsweb/project/index");
    },
    switchProject(row) {
      this.$store.commit("SWAICH_PROJECT", {
        id: row.id,
        name: row.name,
      });
      this.$message({
        type: "success",
        message: "",
      });
      clearTimeout(this.timer); //
      this.timer = setTimeout(() => {
        //
        this.$router.go(0);
      }, 1000);
    },
    handleEdit(index, row) {
      this.$router.push({
        path: "edit",
        query: {
          itemId: row.id,
        },
      });
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          deleteProject(row.id).then(
            this.tableData.splice(index, 1),
            this.$message({
              type: "success",
              message: "!",
            })
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.defaultPageSize = val;
      getProject(this.defaultPageSize, 0).then((res) => {
        this.tableData = res.results;
        this.totalItems = res.count;
      });
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      getProject(this.defaultPageSize, (val - 1) * this.defaultPageSize).then(
        (res) => {
          this.tableData = res.results;
          this.totalItems = res.count;
        }
      );
    },
  },
};
</script>
