<template>
  <div>
    <el-page-header
      style="line-height: 40px; color: silver"
      @back="goBack"
      content=""
    >
    </el-page-header>
    <br />
    <br />
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column fixed prop="name" label="" width="200">
      </el-table-column>
      <el-table-column prop="desc" label="" width="350">
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ getProtocol(scope.row.addr) }}</el-tag>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="" width="100">
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <el-tag type="success" size="medium">{{
              scope.row.request_mode
            }}</el-tag>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="addr" label="" width="700"> </el-table-column>
      <el-table-column fixed="right" label="" width="150">
        <template slot-scope="scope">
          <el-button
            @click="handleEdit(scope.$index, scope.row)"
            type="text"
            size="medium"
            ></el-button
          >
          <el-button
            @click="handleDelete(scope.$index, scope.row)"
            type="text"
            size="medium"
            style="color: orangered"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-row style="margin-top: 30px" type="flex" justify="end">
      <el-pagination
        background
        layout="total, sizes, prev, pager, next"
        :total="totalItems"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="defaultPageSize"
      >
      </el-pagination>
    </el-row>
  </div>
</template>

<script>
import { listInterface, deleteInterface } from "network/interface";
export default {
  mounted() {
    let currentProjectID = sessionStorage.getItem("currentProjectID");
    if (currentProjectID) {
      this.currentProjectID = currentProjectID;
      listInterface(currentProjectID, 0, this.defaultPageSize).then((res) => {
        this.totalItems = res.count;
        this.tableData = res.results;
      });
    }
  },
  computed: {
    getProtocol() {
      return function (value) {
        if (value.indexOf("https://") == 0) {
          return "HTTPS";
        } else {
          return "HTTP";
        }
      };
    },
  },
  methods: {
    goBack() {
      this.$router.push("/toolsweb/interface/list");
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.defaultPageSize = val;
      listInterface(this.currentProjectID, 0, this.defaultPageSize).then(
        (res) => {
          this.tableData = res.results;
          this.totalItems = res.count;
        }
      );
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      listInterface(
        this.currentProjectID,
        (val - 1) * this.defaultPageSize,
        this.defaultPageSize
      ).then((res) => {
        this.tableData = res.results;
        this.totalItems = res.count;
      });
    },
    handleEdit(index, row) {
      this.$router.push({
        path: "edit",
        query: {
          itemId: row.id,
        },
      });
    },
    handleDelete(index, row) {
      console.log(row);
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          console.log(row.id);
          deleteInterface(row.id)
            .then((res) => {
              console.log(res);
              this.tableData.splice(index, 1);
              this.$message({
                type: "success",
                message: "!",
              });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "error",
                message: "!",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
  data() {
    return {
      defaultPageSize: 10,
      currentPage: 1,
      totalItems: 0,
      tableData: Array(),
    };
  },
};
</script>
